details {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid whitesmoke;
  transition: all 1s;
}
details.no-border {
  border-bottom: none;
}
details:not([open]) summary ~ * {
  animation-name: sweep-out;
  animation-duration: 0.5s ease-in-out;
}
details[open] summary ~ * {
  animation: sweep 0.5s ease-in-out;
}
details[open] summary:after {
  content: "▾";
  font-size: 1.2rem;
}
details summary {
  position: relative;
  cursor: pointer;
  font-size: 24px;
  font-weight: 600;
  outline: none;
}
details summary::-webkit-details-marker {
  display: none;
}
details summary:after {
  content: "▸";
  font-size: 1.2rem;
  float: right;
  padding: 0 2rem 0 0;
  transition: all 1s linear;
}
details summary.summary--info {
  color: hsl(220deg, 92%, 51%);
}
details summary.summary--error {
  color: hsl(357deg, 47%, 48%);
}
details summary.summary--warning {
  color: hsl(29deg, 97%, 54%);
}
details summary.summary--success {
  color: hsl(141deg, 50%, 35%);
}
details p {
  font-size: 1.5rem;
  line-height: 2rem;
  padding: 1rem 2rem 1rem 0;
}

@keyframes sweep {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes sweep-out {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-10px);
  }
}
.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin: 4rem 5vw;
  padding: 0;
  list-style-type: none;
}

.card {
  position: relative;
  display: block;
  height: 100%;
  padding: 1rem;
  border: 1px solid whitesmoke;
  border-radius: calc(var(--curve) * 1px);
  overflow: hidden;
  text-decoration: none;
}

.card__image {
  width: 100%;
  height: auto;
}

.card__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  border-radius: calc(var(--curve) * 1px);
  background-color: hsl(0deg, 0%, 99%);
  transform: translateY(100%);
  transition: 0.2s ease-in-out;
}

.card:hover .card__overlay {
  transform: translateY(0);
}

.card__header {
  position: relative;
  display: flex;
  align-items: center;
  gap: 2em;
  padding: 2em;
  border-radius: calc(var(--curve) * 1px) 0 0 0;
  background-color: hsl(0deg, 0%, 99%);
  transform: translateY(-100%);
  transition: 0.2s ease-in-out;
}

.card__arc {
  width: 40px;
  height: 40px;
  position: absolute;
  bottom: 100%;
  right: 0;
  z-index: 1;
}

.card__arc path {
  fill: hsl(0deg, 0%, 99%);
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}

.card:hover .card__header {
  transform: translateY(0);
}

.card__thumb {
  flex-shrink: 0;
