  height: 50px;
  border-radius: 50%;
}

.card__title {
  font-size: 1em;
  margin: 0 0 0.3em;
  color: hsl(240deg, 92%, 51%);
}

.card__tagline {
  display: block;
  margin: 1em 0;
  font-size: 0.8em;
  color: hsl(0deg, 0%, 18%);
}

.card__status {
  font-size: 0.8em;
  color: hsl(0deg, 0%, 18%);
}

.card__description {
  padding: 0 2em 2em;
  margin: 0;
  color: hsl(0deg, 0%, 18%);
