.alert-validate::before {
  content: attr(data-validate);
  position: absolute;
  max-width: 70%;
  background-color: hsl(0deg, 0%, 99%);
  border: 1px solid hsl(357deg, 47%, 48%);
  border-radius: 2px;
  padding: 4px 30px 4px 10px;
  bottom: calc((100% - 25px) / 2);
  -webkit-transform: translateY(50%);
  -moz-transform: translateY(50%);
  -ms-transform: translateY(50%);
  -o-transform: translateY(50%);
  transform: translateY(50%);
  right: 2px;
  pointer-events: none;
  color: hsl(357deg, 47%, 48%);
  font-size: 14px;
  line-height: 1.4;
  text-align: left;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  -o-transition: opacity 0.4s;
  -moz-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

.alert-validate::after {
  content: "\f1f4";
  font-family: Material-Design-Iconic-Font;
  display: block;
  position: absolute;
  color: #c80000;
  font-size: 18px;
  bottom: calc((100% - 25px) / 2);
  -webkit-transform: translateY(50%);
  -moz-transform: translateY(50%);
  -ms-transform: translateY(50%);
  -o-transform: translateY(50%);
  transform: translateY(50%);
  right: 8px;
}

.alert-validate:hover:before {
  visibility: visible;
  opacity: 1;
}

@media (max-width: 992px) {
  .alert-validate::before {
    visibility: visible;
    opacity: 1;
  }
}
.true-validate::after {
  content: "\f26b";
  font-family: Material-Design-Iconic-Font;
  font-size: 22px;
  color: #00ad5f;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: calc((100% - 25px) / 2);
  -webkit-transform: translateY(50%);
  -moz-transform: translateY(50%);
  -ms-transform: translateY(50%);
  -o-transform: translateY(50%);
  transform: translateY(50%);
  right: 5px;
}

.message {
  position: relative;
  width: calc(100% - 3em);
  max-width: 40em;
  padding: 1.5em 1em 1.5em 2em;
  border: 1px solid #2e2e2e;
  border-left-width: 7px;
  border-left-style: solid;
  border-radius: 3px;
  line-height: 1.5;
  color: #2e2e2e;
  opacity: 1;
  margin-top: 2em;
}
.message:before {
  color: white;
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: 50%;
  left: -3px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  line-height: 2rem;
  text-align: center;
  font-size: 1.5rem;
}
.message__close {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 1rem;
  color: #2e2e2e;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  animation-name: hidemessage;
  animation-duration: 1s;
}
.message__close span {
  font-size: 1rem;
}
.message p {
  color: #2e2e2e;
  margin: 0 0 1em;
  transition: all 3s ease-out;
}
.message p:last-child {
  margin-bottom: 0;
}

.message--info {
  background: linear-gradient(to bottom, hsla(220deg, 92%, 51%, 0.3), hsla(220deg, 92%, 51%, 0.1));
  border-color: hsl(220deg, 92%, 51%);
}
.message--info:before {
  background-color: hsl(220deg, 92%, 51%);
  content: "\f1f8";
  font-family: "Material-Design-Iconic-Font", sans-serif;
}

.message--info .message__close {
  color: hsl(220deg, 92%, 51%) !important;
  font-weight: 700;
}

.message--error {
  background: linear-gradient(to bottom, hsla(357deg, 47%, 48%, 0.3), hsla(357deg, 47%, 48%, 0.1));
  border-color: hsl(357deg, 47%, 48%);
}
.message--error:before {
  background-color: hsl(357deg, 47%, 48%);
  content: "\f1f1";
  font-family: "Material-Design-Iconic-Font", sans-serif;
}

.message--error .message__close {
  color: hsl(357deg, 47%, 48%) !important;
  font-weight: 700;
}

.message--warning {
  background: linear-gradient(to bottom, hsla(29deg, 97%, 54%, 0.3), hsla(29deg, 97%, 54%, 0.1));
  border-color: hsl(29deg, 97%, 54%);
}
.message--warning:before {
  background-color: hsl(29deg, 97%, 54%);
  content: "\f1fe";
  font-family: "Material-Design-Iconic-Font", sans-serif;
}

.message--warning .message__close {
  color: hsl(29deg, 97%, 54%) !important;
  font-weight: 700;
}

.message--success {
  background: linear-gradient(to bottom, hsla(141deg, 50%, 35%, 0.3), hsla(141deg, 50%, 35%, 0.1));
  border-color: hsl(141deg, 50%, 35%);
}
.message--success:before {
  background-color: hsl(141deg, 50%, 35%);
  content: "\f269";
  font-family: "Material-Design-Iconic-Font", sans-serif;
}

.message--success .message__close {
  color: hsl(141deg, 50%, 35%) !important;
  font-weight: 700;
}

input[type=checkbox].hide-input:checked + .message {
  animation-duration: 1s;
  animation-direction: ease-out;
  animation-play-state: start;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-name: hidemessage;
  overflow: auto;
}

input.hide-input {
  display: none;
}

.message label.toggle {
  user-select: none;
}

.autohide {
  animation-name: hidemessage;
  animation-direction: ease-out;
  animation-delay: 5s;
  animation-duration: 1s;
  animation-play-state: start;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
}

@keyframes hidemessage {
  0% {
    margin-left: 0%;
  }
  50% {
    margin-left: -555%;
  }
  100% {
    padding: 0;
    opacity: 0;
    border: 0;
    margin: 0;
    margin-left: -999%;
    height: 0;
    /**line-height: 0;
    overflow: hidden;**/
  }
}
/* breakpoints */
.tabs {
  position: relative;
  padding: 50px;
  padding-bottom: 80px;
  min-width: 240px;
}
.tabs input[name=tab-control] {
  display: none;
}
.tabs .content section h2,
.tabs ul li label {
  font-weight: bold;
  font-size: 18px;
  color: hsl(240deg, 92%, 51%);
}
.tabs ul {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
}
.tabs ul li {
  box-sizing: border-box;
  flex: 1;
  width: 25%;
  padding: 0 10px;
  text-align: center;
}
.tabs ul li label {
  transition: all 0.3s ease-in-out;
  color: #79798b;
  padding: 5px auto;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  cursor: pointer;
  white-space: nowrap;
  -webkit-touch-callout: none;
}
.tabs ul li label br {
  display: none;
}
.tabs ul li label svg {
  fill: #79798b;
  height: 1.2em;
  vertical-align: bottom;
  margin-right: 0.2em;
  transition: all 0.2s ease-in-out;
}
.tabs ul li label:hover, .tabs ul li label:focus, .tabs ul li label:active {
  outline: 0;
  color: #a2a2ae;
}
.tabs ul li label:hover svg, .tabs ul li label:focus svg, .tabs ul li label:active svg {
  fill: #a2a2ae;
}
.tabs .slider {
  position: relative;
  width: 25%;
  transition: all 0.33s cubic-bezier(0.38, 0.8, 0.32, 1.07);
}
.tabs .slider .indicator {
  position: relative;
  width: 50px;
  max-width: 100%;
  margin: 0 auto;
  height: 4px;
  background: hsl(240deg, 92%, 51%);
  border-radius: 1px;
}
.tabs .content {
  margin-top: 30px;
}
.tabs .content section {
  display: none;
  animation-name: content;
  animation-direction: normal;
  animation-duration: 0.3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
  line-height: 1.4;
}
.tabs .content section h2 {
  color: hsl(240deg, 92%, 51%);
  display: none;
}
.tabs .content section h2::after {
  content: "";
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background: hsl(240deg, 92%, 51%);
  margin-top: 5px;
  left: 1px;
}
.tabs input[name=tab-control]:nth-of-type(1):checked ~ ul > li:nth-child(1) > label {
  cursor: default;
  color: hsl(240deg, 92%, 51%);
}
.tabs input[name=tab-control]:nth-of-type(1):checked ~ ul > li:nth-child(1) > label svg {
  fill: hsl(240deg, 92%, 51%);
}
